<template>
	<view class="login">
		<view class="wrapper-bd">
			<image class="login-img" src="https://s1.music.126.net/style/favicon.ico?v20180307"></image>
		</view>
		<view class="box">
			<form @submit="sarchBox">
				<view class="login-input">
					<input class="login-emial" placeholder-class="pla-class" type="text" placeholder="163邮箱" name="username" />
					<input class="login-pwd" placeholder-class="pla-class" type="password" placeholder="密码" name="pwd" />
				</view>
				<button form-type="submit" class="default" style="width: 75vw;">登录</button>
			</form>
		</view>
		<view class="login-methods-row">
			<view class="email-login" @click="handlePwd">
				<text class="iconfont icon-youxiang login-icon"></text>
				<text class="email-title">密码登录</text>
			</view>
			<view class="code-login" @click="handleCode">
				<text class="iconfont icon-yanzhengma2 login-icon"></text>
				<text class="code-title">验证码登录</text>
			</view>
		</view>
	</view>
</template>

<script>
	import app from '../../App.vue'
	
	import {getEmailLogin} from '../../service/login.js'
	
	export default {
		data() {
			return {
				autoFocus: true,
				username: '',
				pwd: '',
				userInfo: {}
			}
		},
		components:{
			 
		},
		methods: {
			// --------------------事件监听--------------------------
			handlePwd(){
				uni.navigateTo({
					url:'./login'
				})
			},
			handleCode(){
				uni.navigateTo({
					url:'./codeLogin'
				})
			},
			sarchBox(e){
				this.username = e.detail.value.username
				this.pwd = e.detail.value.pwd
				this._getEmailLogin(this.username,this.pwd)
			},
			// --------------------网络请求--------------------------
			_getEmailLogin(email,password){
				if(/^[0-9a-zA-Z]\w{5,17}@163.com/.test(this.username)){
					getEmailLogin(email,password).then(res=>{
						let code=res.data.code
						if(code==200){
							console.log(res)
							console.log('20000000')
							uni.setStorageSync('userId',res.data.account.id)
							app.globalData.userId=res.data.account.id
							this.saveUserLoginInfo(res.cookies)
							console.log('-------------------------')
							app.globalData.navId=1
							uni.redirectTo({
								url:'../profile/profile'
							})
						}else{
							uni.showToast({
								title: '账号与密码不一致',
								icon: 'none'
							})
						}
					}).catch(err=>{
						uni.showToast({
							title: err,
							icon: 'none',
							mask: true,
							duration: 2000
						})
					})
				}else{
					console.log(this.username)
					uni.showToast({
						title:'请输入163邮箱',
						icon:'none'
					})
				}
			},
			// 保存登录cookie
			saveUserLoginInfo(cookies) {
				for (let i = 0; i < cookies.length; i++) {
					if (cookies[i].search('MUSIC_U=') != -1) {
						uni.setStorageSync('login_token', cookies[i])
						console.log(uni.getStorageSync('login_token'))
						app.globalData.login_token = cookies[i]
					}
				}
			}
		}
	}
</script>

<style>
	@import url("../../static/iconfont/iconfont.css");
	.login {
		color: rgba(255, 255, 255, .5);
		height: 100vh;
		background-color: #DC2C1D;
	}

	.wrapper-bd {
		width: 100%;
		height: 50vh;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.login-img {
		width: 120rpx;
		height: 120rpx;
	}

	.login-input {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
	}

	.login-emial,
	.login-pwd {
		width: 70%;
		height: 70rpx;
		border-bottom: 1rpx solid #ccc;
		margin-bottom: 20rpx;
		color: #ccc;
	}

	.default {
		margin-top: 30rpx;
		border-radius: 50rpx;
		font-weight: 500;
		color: #DD011C;
	}

	.pla-class {
		color: #ccc;
	}
	.login-methods-row{
		width: 90%;
		height: 100rpx;
		margin: 20rpx auto;
		display: flex;
		justify-content: space-around;
	}
	.email-login{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.code-login{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.login-icon{
		font-size: 40rpx;
	}
	.code-title,
	.email-title{
		font-size: 20rpx;
	}
</style>
